
  body {
	margin: 0;
	padding: 0;
  }
  
  button:hover {
	cursor: pointer;
  }
  
  /* START OF NAVBAR CSS */
  .Navbar {
	background-color: #333399;
	width: 100%;
	height: 100px;
	font-family: Arial, Helvetica, sans-serif;
	display: flex;
  }
  
  /* 50% of screen dedicated to leftSide */
  .Navbar .rightSide {
	flex: 50%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	padding-right: 25px;
  }
  
  .Navbar .rightSide .links {
	max-height: 80px;
  }
  
  .Navbar .rightSide .links a {
	text-decoration: none;
	color: white;
	font-size: 25px;
	margin-left: 15px; /* space between links */
  }
  
  /* Sets default state of button to invisible, it should show only when window
  is small */
  .Navbar .rightSide button {
	display: none;
	margin-left: 20px;
  }
  
  .Navbar .rightSide #hidden {
	display: flex;
  }
  
  /* 50% of screen dedicated to right side of nav bar */
  .Navbar .leftSide {
	flex: 50%;
	display: flex;
	justify-content: left;
	align-items: center;
  }
  
  .Navbar .leftSide .logo {
	height: 100px;
	width: 430px;
	max-width: 100%;
	height: auto;
  }
  
  
  /* When screen is less than 650px, show the button and hide the links */
  @media only screen and (max-width: 900px) {
	.Navbar .rightSide button {
	  display: flex;
	}
	.Navbar .rightSide .links {
	  display: none;
	}
	.Navbar .rightSide #hidden {
	  position: absolute;
	  left: 0px;
	  top: 80px;
	  height: 100%;
	  max-height: 100%;
	  width: 100%;
	  background-color: #333399;
	  display: flex;
	  flex-direction: column;
	  align-items: center;
	}
	.Navbar .rightSide #hidden a {
	  margin: 10px;
	  font-size: 25px;
	}
  }
  
  /* END OF NAVBAR CSS */
  
  /* START OF SEARCH BAR CSS */
  
  .SearchBar {
	padding-top: 25px;
	display: flex;
	flex-direction: column;
	align-items: center;
  }
  
  .SearchBar input {
	width: 50%;
	height: 35px;
	font-size: 24px;
	padding-left: 10px;
  }